import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import { AdaptivityProvider, AppRoot, Button, Footer, Link, Panel, PanelHeader } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import image from './img/screen.png'

import './App.css'


const App = () => {
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	let [link, setLink] = useState(null);

	useEffect(() => {
		setPopout(null)
		let hash = window.location.hash.slice(1, window.location.hash.length)
		let links = hash.split('&')
		setLink(<Link target="_blank" href={links[1]} style={{margin: '15px auto'}}>
		<Button >Скачать</Button>
		</Link>)
		console.log(links);
		bridge.send('VKWebAppShowNativeAds', { ad_format: 'interstitial' })
	}, []);


	return (
		<AdaptivityProvider>
			<AppRoot>
				<View activePanel='home' popout={popout}>
					<Panel id='home'>
						<PanelHeader>Voice Downloader</PanelHeader>
						<Footer>Для того чтобы скачать голосовое сообщение, нажмите на кнопку "Скачать", 
							в открывшемся окне нажмите на 3 точки и выберите пункт "Скачать"
							(Недоступно на IOS)
						</Footer>
						<img className="image" src={image} alt='img'/>
						
						{link}
					</Panel>
				</View>
			</AppRoot>
		</AdaptivityProvider>
	);
}

export default App;

